require('dotenv').config();


export default function fetchAdd(){
    if(!('fetch' in window)){
        console.log('error');
        return;
    }

}
export function getAlbums() {
    const requestURL = 'https://druloloy.github.io/JSON_API/albums.json'; // API URL

    fetch(requestURL) // use fetch to get connection to the API
        .then(response=>response.json()) // response: the data returned by the fetch, it contains data gathered from the API. We used .json() to parse the data into JSON. 
        .then(data=>{ 
            // to get the exact data, we need to have another promise
            const albumHolder = data.map(album=>{
                return `
                <div class="album">
                    <img src="${album["album art"]}" alt="${album.title}">

                    <h4 id="title">${album.title}</h4>
                    <p id="year-published">${album["year released"]}</p>
                </div>
                `
            })
            .join("");
        // breakdown all data inside the array JSON and make a new div for it's container. We use join to remove impurities between data containers.
        document.querySelector('.albums-container').insertAdjacentHTML('afterbegin', albumHolder); // just like appendChild but it can take whole HTML
    })
    .catch(err=>{
        console.log("error");
    });
}

export async function getTours(){

    const requestURL = 'https://druloloy.github.io/JSON_API/tours.json';

    await fetch(requestURL)
        .then(res=>res.json())
        .then(data=>{
            const tourHolder = data.map(tour=>{
                return `
                <div class="tours-card">
                <h4 id="date">${tour.date} <span class="highlight">${tour.weekday}</span></h4>
                <P id="venue">${tour.state}, ${tour.country}</P>
                <p id="venue-exact">${tour.venue}</p>   
            </div>
                `
            }).join("");

            document.querySelector('.tours-card-container').insertAdjacentHTML('afterbegin',tourHolder);
        })
    .catch(err=>console.log(err));
}
export async function  getNews(){
    const api_key = process.env.NEWS_API_KEY;
    const requestURL = `https://gnews.io/api/v4/search?q="panic+at+the+disco"&token=`;
    await fetch(requestURL+api_key)
    .then(response=>response.json())
    .then(data=>{
        const news_card = data.articles.map(news=>{
            
                return `
                <a target="_blank" href="${news.url}">
                    <div class="news-card">

                        <img src="${news.image}" alt="${news.title}" />
                        <h4 class="article-header">${news.title}</h4>
                        <p class="article-desc">${news.description}</p>
                        <p class="article-source">${news.source.name}</p>
                    </div> 
                </a>
            `
        }).join("");
        
        document.querySelector('.news-card-container').insertAdjacentHTML('afterbegin', news_card);
    })
    .catch(e=>console.log(e))
}